/* CartPage.css */
.cart-page {
  margin: 20px;
  font-family: Arial, sans-serif;
}

.cart-page h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 20px;
}

.empty-cart {
  text-align: center;
  padding: 20px;
  font-size: 30px;
  color: white;
}

.cart-items {
  list-style-type: none;
  padding: 0;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.cart-item-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-right: 20px;
  border-radius: 8px;
}

.cart-item-details {
  flex-grow: 1;
}

.cart-item-details h3 {
  margin: 0;
  font-size: 1.2rem;
}

.cart-item-details p {
  margin: 5px 0;
  color: white;
}

.remove-btn {
  background-color: #e74c3c;
  color: white;
  padding: 5px 10px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.remove-btn:hover {
  background-color: #c0392b;
}

.cart-summary {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  font-size: 1.2rem;
}

.clear-cart,
.checkout-btn {
  background-color: #4caf50;
  color: white;
  padding: 5px 10px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: large;
  
}
.quantity{
  color: white;
}

.clear-cart:hover,
.checkout-btn:hover {
  background-color: #5abd5b;
}

.quantity-btn{
  margin: 5px;
  padding: 7px;
  background-color: white;
  color: black;
}